import { useState } from 'react';
import './App.css';
import Popup from './common/Popup';
import Listli from './common/Listli';
import userEvent from '@testing-library/user-event';

function App() {
  const[list, setList] = useState([
    "",
    "  /  ",
    "  ",
  ]);

  const [count, setCount] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  const [listnum, setListnum] = useState(0);
  const [inText, setInText] = useState("");

  function writeList() {
    if (inText == "") {
      alert("  .");
      return;
    }
    else {
      let copy = [...list];
    copy.unshift(inText);
    setList(copy);
    setInText("");
    }
  }
  
  function delList(i) {
    let copy = [...list];
    copy.splice(i,1);
    setList(copy);
  }

  return (
    <div className='wrap'>
    <header>
        <h1>
            <span></span>
            <span></span>
        </h1>
    </header>
    {/* <button onClick={() => setIsOpen(true)}></button>
    <button onClick={() => setIsOpen(false)}></button>
    <button onClick={() => setIsOpen(!isOpen)}>toggle</button> */}
    <main>
        <div className="textInput">
            <input 
            type="text" 
            value={inText}
            onChange={(e) => {
              e.preventDefault();
              setInText(e.target.value);
            }}
            placeholder=' ?'
            onCanPlay={(e) => {
              e.preventDefault();
            }}
            ></input>
            <button onClick={ writeList }
            >
              </button>
        </div>
        <p className="total">
             <span>{list.length}</span></p>
        <ul className="list">
          {list.map((list,i) => {
            return <Listli 
            list={list} setIsOpen={setIsOpen} 
            setListnum={setListnum}
            key={i}
            i={i}
            delList={delList}
            />
          })}
        </ul>
        {list.length == 0 ? <p style={{ textAlign: "center"}}> .</p> : null}
    </main>
    {
        isOpen == false ? null : 
        <Popup list={list} setIsOpen={setIsOpen} listnum={listnum} />
    }
    </div>
  );
}

export default App;
