export default function Listli({list, setIsOpen, setListnum, i, delList}) {
    return (
        <li>
            <p 
            onClick={() => {
                setListnum(i)
                setIsOpen(true)
              }}
            >{list}</p>
            <i className="fa-solid fa-trash-can" 
            onClick={() => {
                delList(i);
              }}
            ></i>
        </li>
    )
}